/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

h1,
h2,
h3,
h4,
h5 {
  font-family: monospace;
  color: #e85a4f; }

h1 {
  font-size: 2.25em;
  text-transform: uppercase;
  font-weight: bold;
  margin: 10px 0 25px; }

h2 {
  font-size: 2em;
  text-transform: uppercase;
  text-decoration: underline;
  margin: 10px 0 25px; }

h3 {
  font-size: 1.75em; }

h4 {
  font-size: 1.5em; }

h5 {
  font-size: 1.25em; }

strong {
  font-weight: bold; }

em {
  font-style: italic; }

p,
input,
em,
b,
blockquote,
pre,
ol,
ul,
li,
table {
  font-family: sans-serif;
  color: #4e4d4b;
  line-height: 1.5rem; }

a {
  color: #e98074;
  text-decoration: none;
  border-bottom: 3px solid #e98074;
  font-weight: bold; }

p {
  margin: 15px 0; }

ul,
ol {
  margin: 10px 0; }
  ul li,
  ol li {
    margin: 10px 0 5px 15px; }

blockquote {
  display: block;
  position: relative;
  font-size: 1.5em;
  padding: 20px 10px 20px 25px; }
  blockquote:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 3px;
    background: #e85a4f; }

article.post {
  padding: 25px;
  background: #eae7dc; }

footer.site-footer {
  width: 100%;
  text-align: center;
  line-height: 1.5rem;
  padding: 10px 0 30px; }
  footer.site-footer nav {
    margin: 15px; }
  footer.site-footer section.copyright {
    font-size: 0.75rem; }
    footer.site-footer section.copyright a {
      border-width: 1px; }

html,
body {
  background: #d8c3a5; }

img {
  max-width: 100%; }

pre {
  max-width: 100%;
  overflow-x: auto; }

body > .wrapper {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 650px; }
